<template>
<div class="rightSidebar">
    <div class="banner-list">
        <banner-li  :list = list[0]></banner-li>
    </div>
    <div class="cateText" v-for="itme in list.slice(2)" :key = itme.id>
        <h3 v-if="itme[0].id != 0">{{itme[0].name}}</h3>
        <ul>
            <li  v-for="(vla,index) in itme && itme.slice(1)" :key ='vla.id' :id='vla.id' @click="deteilsAction(vla.id,index)" >
                <img v-lazy="vla.wapBannerUrl" alt="">
                <span>{{vla.name}}</span>
            </li>
        </ul>
    </div>
</div>
    
</template>

<script>
import banner from '../../../../components/banner';
import { mapActions,mapMutations  } from 'vuex'
export default {
    name:'rightSidebar-itme',
    components:{
        [banner.name]:banner,
    },
    props:{
        list:Array,
    },
    computed:{
        caId:function(){
           return this.list[1];
        }
    },
    methods:{
        ...mapActions({
            requesCateDetails:'Cate/requesCateDetails',
        }),
        ...mapMutations({
            setCateDetailsfalg:'Cate/setCateDetailsfalg',
            setCateNavIndex:'Cate/setCateNavIndex'
        }),
        deteilsAction(suId,index){
            let url = '/api/category/listmap/group/item?categoryId='+ this.caId +'&subCategoryId=' + suId;
            this.setCateDetailsfalg(false);
            this.setCateNavIndex(index)
            this.requesCateDetails(url);
            this.$router.push('/cate/details?categoryId=' + this.caId +'&subCategoryId=' + suId );
        }
    },
}
</script>

<style lang='scss' scoped>
.rightSidebar{
    padding:0 3vw 5vw;
    overflow: hidden;
    position: relative;
    top:  5vw;
}
.cateText{
    margin-top: 3vw;
}
h3{
    font-weight: bold;
    border-bottom: 1px solid #d9d9d9;
    font-size: 14px;
    padding: 10px 0;
}
ul{
    margin-top: 15px;
    background: #fff;
}
li{
    display: inline-block;
    width: 33%;
    box-sizing: border-box;
    vertical-align: top;
    font-size: 12px;
    text-align: center;
    margin-bottom: 5vw;
    span{
        display: block;
        padding-left: 3vw;
        line-height: 5vw;
        border-left: 1vw solid transparent;
    }
    img{
        width: 100%;
    }
}

</style>
