<template>
    <ul>
        <li v-for="(itme,index) in list" :key = itme.id  >
            <span :class="{action:index == sideIndex}" @click="leftSidebarAction(index,itme.id)">{{itme.name}}</span>
        </li>
    </ul>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    name:'leftSidebar-itme',
    props:{
        list:Array,
    },
    data(){
        return {
            sideIndex : 0,
        }
    },
    methods:{
        leftSidebarAction(index,id){
            this.sideIndex = index;
            this.requesrightlistmap(id);
        },
        ...mapActions({
            requesrightlistmap:'Cate/requesrightlistmap'
        }),
    }
}
</script>

<style lang="scss" scoped>
ul{
    background: #fff;
}
li{
    padding:5vw 0;
    text-align: left;
    width: 100%;
    span{
        display: block;
        padding-left: 3vw;
        line-height: 5vw;
        border-left: 1vw solid transparent;
    }
    span.action{
        color: #ab2b2b;
        border-left: 1vw solid #ab2b2b;
    }
}


</style>

