<template>
    <div>
        <!-- 头部搜索 -->
        <div class='home-search' @click="routerAction" >
            <div>
                <i></i><span>搜索商品, 共21526款好物</span>
            </div>
        </div>
        <!-- 主体内容 -->
        <div id='cate' class="conten pages-top">
            <!-- 左侧菜单栏 -->
            <srcoll-itme class="left">
                <leftSidebar-itme :list='cateleftlistmap'></leftSidebar-itme>
            </srcoll-itme>
            <!-- 右侧菜单栏 -->
            <srcoll-itme class="rigth">
                <rightSidebar-itme :list='caterightlistmap'></rightSidebar-itme>
            </srcoll-itme>
        </div>
    </div>
    

</template>

<script>
import scroll from '../../../components/scroll';
import leftSidebar from './childre/leftSidebar';
import rightSidebar from './childre/rightSidebar';

import { mapState , mapActions } from 'vuex';
export default {
    components:{
        [scroll.name]:scroll,
        [leftSidebar.name]:leftSidebar,
        [rightSidebar.name]:rightSidebar,

    },
    methods:{
        ...mapActions({
            requesleftlistmap:'Cate/requesleftlistmap',
            requesrightlistmap:'Cate/requesrightlistmap'
        }),
        //搜索页跳转
        routerAction(){
            this.$router.push('/search');
        }
    },
    computed:{
        ...mapState({
            cateleftlistmap:state=>state.Cate.cateleftlistmap,
            caterightlistmap:state=>state.Cate.caterightlistmap,
        })
    },
    created(){
        this.requesleftlistmap();
        this.requesrightlistmap();
    }
}
</script>

<style lang="scss" scoped>
#cate{
    margin-top: 1px;
    overflow: hidden;
    display: flex;
    background: #fff;
    .left{
        width: 22%;
    }
    .rigth{
        flex: 1;
        padding-left: 2vw;
    }
}
.home-search{
        background: #fff;
        height:49px;
        color: #666;
        text-align: center;
        width: 100%;
        padding: 3vw 3vw;
        box-sizing: border-box;
        div{
            
            width: 100%;
            background: #ededed;
            padding: 5px 0;
            border-radius: 5px;
            i{
                display: inline-block;
                vertical-align: middle;
                background-image: url('/images/searchIcon.png');
                background-repeat: no-repeat;
                background-size: 100% 100%;
                width: 14px;
                height: 14px;
                margin-right: 6px;
               
            }
        }
        
    }

</style>

